@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
* {
  box-sizing: border-box;
  font-size: 10px;
  font-family: "Roboto", sans-serif;
  margin: 0;
}

.home-screen-container {
  width: 100vw;
  height: 100vh;

  display: flex;
}

.draggable-element {
  width: 100%;
  height: 5rem;

  display: flex;
  align-items: center;

  padding: 2rem 1.5rem;

  background-color: rgba(240, 240, 240, 1);

  transition: all 0.2s;
}

.draggable-element:hover {
  transform: scale(1.02);
  background-color: rgba(255, 255, 255, 1);
}

.draggable-element:active {
  background-color: rgba(255, 255, 255, 0.8);
}

.draggable-element-icon {
  width: 1.3rem;
  height: 2rem;
  margin-right: 0.5rem;

  fill: #aaa;
}

.draggable-element-title {
  font-size: 1.5rem;
  text-transform: capitalize;
}

.list-container {
  width: 20%;
  height: 100%;

  background-color: rgb(45, 45, 45);

  display: flex;
  flex-direction: column;

  padding: 2rem 1.5rem;

  z-index: 100000;
}

.list-container > *:not(:last-child) {
  margin-bottom: 2rem;
}

.list-container-title {
  color: #fff;
  font-size: 2rem;
  font-weight: 400;
  text-transform: uppercase;
}

.canvas-container {
  width: 80%;
  height: 100%;

  background-color: rgb(240, 240, 240);

  position: relative;
}

.button-component {
  width: 100%;
  height: 100%;

  display: flex;
  justify-content: center;
  align-items: center;

  border-radius: 0.5rem;

  background-color: rgb(20, 45, 200);
  color: #fff;

  padding: 0.5rem 1rem;
}

.input-component {
  width: 100%;
  height: 100%;

  min-width: 10rem;
  min-height: 2.6rem;

  border: 0.1rem solid #555;
  border-radius: 0.3rem;
}

.input-component::-webkit-input-placeholder {
  color: #555;
}

.input-component:-ms-input-placeholder {
  color: #555;
}

.input-component::placeholder {
  color: #555;
}

.label-component {
  width: 100%;
  height: 100%;

  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.element-component {
  width: 10rem;
  height: 4rem;
}

.element-component-common {
  transition: transform 0.5s;
  transform: translate(-50%, -50%);
}

.element-component-input {
  min-width: 20rem;
  min-height: 3.5rem;
}

.element-component-label {
  width: auto;
  height: auto;
}

.element-component-button {
  min-width: 10rem;
  min-height: 4rem;
}

.modal-container {
  position: absolute;

  width: 100vw;
  height: 100vh;

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: rgba(0, 0, 0, 0.5);

  z-index: 300000;
}

.modal-component {
  width: 35%;
  height: auto;

  padding-bottom: 2rem;

  background-color: #fff;

  display: flex;
  flex-direction: column;

  border-radius: 1rem;
}

.modal-component-header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  height: 6rem;
  padding: 0rem 3rem;

  border-bottom: 0.1rem solid #eee;

  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;

  background-color: rgb(252, 252, 252);
}

.modal-component-header-value {
  font-size: 1.7rem;
  color: #444;
  text-transform: capitalize;
}

.modal-component-header-icon {
  width: 1.3rem;
  margin-right: 0.5rem;

  cursor: pointer;

  fill: #aaa;
}

.modal-component-body {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  padding: 0.5rem 3rem;
}

.modal-component-body > *:not(:last-child) {
  margin-bottom: 2rem;
}

.modal-component-body-button {
  width: 20rem;

  font-size: 1.7rem;
  font-weight: 100;
}

.modal-component-body-error {
  width: 100%;
  height: 1.7rem;
  color: #a00;

  font-size: 1.7rem;
  font-weight: 100;
}

.labelled-input-container {
  width: 100%;

  display: flex;
  flex-direction: column;

  font-size: 1.7rem;
  font-weight: 100;
}

.labelled-input-container > *:not(:last-child) {
  margin-bottom: 0.5rem;
}

